@import '~antd/dist/antd.css';

body {
  background-color: #f7dda3;
  /* background-image: radial-gradient(circle, #e1f1fc 0%, #c5d4de 70%, #b6c3cc 100%); */
  background-image: radial-gradient(circle, #ebfaff 0%, #dfedf2 70%, #c7d4d9 100%);
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-logo {
  height: 75px;
  width: 75px;
  pointer-events: none;
  padding: 1vmin;
}

.TitleContainer {
  font-family: Raleway;
  margin: 17.5px;
  
}

.ImageContainer {
  height: 75px;
  width: 75px;
  float: left;
  display: block;
}

.TitleTextContainer {
  height: 75px;
  padding: 5px;
  padding-left:10px;
  width: 110px;
  display: inline-block;
  text-align: left;

}

.parentContainer {
  width: 220px;
  margin-top: 8vmin;
}

.Introduction {
  margin-bottom: 20px;
}

.unsubscribeText {
  margin-bottom: 5px;
}

#unsubscribeButton {
  cursor: pointer;
}

#unsubscribeMessage {
  display: none;
}

.InputField {
  width:210px;
}

.InputContainer {
  margin-bottom: 20px;
  width:210px;
  margin-left: 5px;
  margin-right: 5px;
}

.RadioButton {
  width: 170px;
}

.RadioContainer {
  margin-bottom: 20px;
  width:170px;
  margin-left: 25px;
  margin-right: 25px;
}

.subscribeButton {
  margin-bottom: 10px;
}

#subscriptionStatusBox {
  visibility: hidden;
  margin-bottom: 10px;
}

.bottomIcons {
  font-size:17.5px;
}

.copyright {
  margin-bottom: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
